<script>
import $ from 'jquery';
import RecommendSwiper from '@/components/RecommendSwiper.vue';
import 'aos/dist/aos.css';
import storageMethods from '../methods/storageMethods';

export default {
  name: 'Home',
  components: {
    RecommendSwiper,
  },
  data() {
    return {
      myFavorite: storageMethods.get() || [],
      gotop: false,
    };
  },
  methods: {
    copyCouponCode() {
      // 舊的寫法已淘汰 execCommand
      // 新的寫法 navigator.clipboard
      const select = (DOM) => document.querySelector(DOM);
      select('#coupon_btn').addEventListener('click', () => {
        navigator.clipboard.writeText(select('#coupon_save').textContent);
        this.showAlert();
      });
    },
    handleScroll() {
      const scrolltop = document.documentElement.scrollTop || document.body.scrollTop;
      if (scrolltop > 30) {
        this.gotop = true;
      } else {
        this.gotop = false;
      }
    },
    toTop() {
      document.getElementById('app').scrollIntoView();
    },
    showAlert() {
      // Use sweetalert2
      this.$swal.fire({
        position: 'center',
        icon: 'success',
        title: '複製成功',
        showConfirmButton: false,
        timer: 2000,
        iconColor: '#236F6B',
      });
    },
  },
  mounted() {
    this.copyCouponCode();
    window.addEventListener('scroll', this.handleScroll, true);
    this.toTop();
    $(document).scroll(() => {
      const scroll = $(this).scrollTop();
      if (scroll > 150) {
        $('#popUp').css('margin-left', '-425px');
        $('#plus').css('margin-left', '0px');
      } else {
        $('#plus').css('margin-left', '0px');
        $('#popUp').css('margin-left', '-425px');
      }
    });
    $('#plus').click(() => {
      $('#popUp').css('margin-left', '0px');
      $('#plus').css('margin-left', '-425px');
    });
    $('#close').click(() => {
      $('#popUp').css('margin-left', '-425px');
      $('#plus').css('margin-left', '0px');
    });
  },
};
</script>

